import React, { useState } from "react";
import { Container, Input, Label, Row, Col, Button } from "reactstrap";
import EditorComponent from "../../shared-components/EditorComponent";
import { useDispatch, useSelector } from "react-redux";
import { useNavigate } from "react-router-dom";
import {
  createComponentList,
  editComponentBasic,
  updateComponentDetails,
} from "../../reducers/componentReducer";
import {
  clearError,
  clearSuccess,
  setError,
  setLoading,
  setSuccess,
} from "../../reducers/commonReducer";
const CreateComponent = (props) => {
  const dispatch = useDispatch();
  const navigate = useNavigate();
  const { userdetails } = useSelector((state) => state.user);
  const { level1, level2, level3 } = props;
  const [categoryTitle, setCategoryTitle] = useState(
    props.componentBasic && props.componentBasic.componentTitle
      ? props.componentBasic.componentTitle
      : ""
  );
  const [categoryName, setCategoryName] = useState(
    props.componentBasic && props.componentBasic.componentHeading
      ? props.componentBasic.componentHeading
      : ""
  );
  const [descriptionData, setDescriptionData] = useState(
    props.componentBasic && props.componentBasic.componentDescription
      ? props.componentBasic.componentDescription
      : ""
  );

  const description = (value, fieldname) => {
    setDescriptionData(value);
  };

  const updateCategoryTitle = (e) => {
    setCategoryTitle(e.target.value);
  };
  const updateCategoryName = (e) => {
    setCategoryName(e.target.value);
  };

  const addComponent = async () => {
    dispatch(setLoading(true));
    const response = await dispatch(
      createComponentList({
        componentTitle: categoryTitle,
        token: userdetails.token,
        componentHeading: categoryName,
        componentDescription: descriptionData,
        level2: level2 ? props.componentId : null,
        level1: level1 ? props.componentId : null,
      })
    );

    if (response.payload.data.error) {
      dispatch(setError(response.payload.data.error));
      setTimeout(() => {
        dispatch(clearError(response.payload.data.error));
      }, 2500);
    }
    if (response.payload.data.success) {
      dispatch(setSuccess(response.payload.data.success));
      setTimeout(() => {
        dispatch(clearSuccess(response.payload.data.success));
      }, 2500);
      navigate(categoryTitle.toLowerCase().replace(" ", "-"));
    }
  };

  const createComponentHandler = async () => {
    if (level1 || level2 || level3) {
      const data = await dispatch(
        updateComponentDetails({
          _id: props.componentBasic._id,
          componentTitle: categoryTitle,
          componentHeading: categoryName,
          componentDescription: descriptionData,
          token: userdetails.token,
          level1,
          level2,
          level3,
        })
      );
      if (data.payload) {
        props.updateData(
          categoryTitle !== props.componentBasic.componentTitle
            ? categoryTitle
                .trim()
                .replace(" ", "-")
                .toLowerCase()
            : null
        );
      }
    } else {
      const data = await dispatch(
        editComponentBasic({
          componentHeading: categoryName,
          componentDescription: descriptionData,
          token: userdetails.token,
        })
      );
      if (data.payload) {
        props.updateData();
      }
    }
  };
  return (
    <Container>
      <Row>
        <Col className="pt-3">
          {(level1 || level2 || level3) && (
            <div className="px-3 pb-3">
              <Label for="categoryTitle">Component Title</Label>
              <Input
                type="text"
                id="categoryTitle"
                placeholder="Enter Component Title"
                defaultValue={categoryTitle}
                onChange={(e) => updateCategoryTitle(e)}
              />
            </div>
          )}

          <div className="px-3 pb-3">
            <Label for="componentName">Component Heading</Label>
            <Input
              type="text"
              id="componentName"
              placeholder="Enter Component Name"
              defaultValue={categoryName}
              onChange={(e) => updateCategoryName(e)}
            />
          </div>

          <EditorComponent
            description={description}
            descriptionData={descriptionData}
            fieldname="description"
            title="Component Description"
          />
          <div className="p-3 text-end">
            <Button
              className="primary-button me-1"
              onClick={() => props.cancelEdit()}
            >
              Cancel
            </Button>
            {props.formType === "create" ? (
              <Button
                className="primary-button"
                disabled={
                  categoryName === undefined ||
                  descriptionData === undefined ||
                  categoryTitle === undefined ||
                  categoryName === "" ||
                  categoryTitle === "" ||
                  descriptionData === ""
                }
                onClick={() => addComponent()}
              >
                Add Child Category
              </Button>
            ) : (
              <Button
                className="primary-button"
                disabled={
                  categoryName === undefined ||
                  descriptionData === undefined ||
                  categoryName === "" ||
                  descriptionData === ""
                }
                onClick={() => createComponentHandler()}
              >
                Update Category
              </Button>
            )}
          </div>
        </Col>
      </Row>
    </Container>
  );
};

export default CreateComponent;
