import React, { useEffect, useState } from "react";
import Base from "../Base";
import ReactLibraryLeftPanel from "../components/ComponentView/ReactLibraryLeftPanel";
import { useParams } from "react-router-dom";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faEllipsisH } from "@fortawesome/free-solid-svg-icons";
import AnimatedElement from "../shared-components/AnimatedElement";
import ComponentDetails from "../components/ComponentView/ComponentDetails";

const Components = () => {
  const { level1, level2, level3 } = useParams();
  const [isClose, setIsClose] = useState(false);

  useEffect(() => {
    setIsClose(false);
  }, [level1, level2, level3]);

  return (
    <>
      <Base>
        <div className="component-container">
          <div
            className={`component-left py-4 px-2 t-color s-back ${
              isClose === false ? "d-none" : "d-block"
            } d-md-block`}
          >
            <AnimatedElement>
              <ReactLibraryLeftPanel
                level1={level1}
                level2={level2}
                level3={level3}
              />
            </AnimatedElement>
          </div>

          <div className="component-right px-2">
            <ComponentDetails />
          </div>
          <div
            className="component-left-drawer"
            onClick={() => {
              setIsClose(!isClose);
            }}
          >
            <span className="s-color px-5 d-inline-block text-center">
              <FontAwesomeIcon
                icon={faEllipsisH}
                className="mt-1 cursor-pointer"
                style={{ padding: "7px 7px" }}
              />
            </span>
          </div>
        </div>
      </Base>
    </>
  );
};

export default Components;
