import React, { useEffect, useState } from "react";
import { useParams, useNavigate } from "react-router-dom";
import CreateComponent from "./CreateComponent";
import parse from "html-react-parser";
import { useDispatch, useSelector } from "react-redux";
import { Button } from "reactstrap";
import {
  getComponentBasic,
  getComponentDetails,
} from "../../reducers/componentReducer";

const ComponentDetails = (props) => {
  const [showEdit, setShowEdit] = useState(false);
  const [showComponentForm, setShowComponentForm] = useState(false);
  const { level1, level2, level3 } = useParams();
  const [componentBasic, setComponentBasic] = useState({});
  const { userdetails } = useSelector((state) => state.user);
  const dispatch = useDispatch();
  const navigate = useNavigate();
  const showEditForm = () => {
    setShowEdit(true);
  };

  const deleteContent = () => {
    //   level3
    //     ? navigate(`/components/${level1}/${level2}/${title}`)
    //     : level2
    //     ? navigate(`/components/${level1}/${title}`)
    //     : level1
    //     ? navigate(`/components/${title}`)
    //     : navigate(`/components`);
    getComponentData();
    setShowEdit(false);
    setShowComponentForm(false);
  };

  const showAddForm = () => {
    setShowComponentForm(true);
  };

  const cancelEdit = () => {
    setShowEdit(false);
    setShowComponentForm(false);
  };

  const getComponentData = async () => {
    const data =
      level1 === undefined && level2 === undefined && level3 === undefined
        ? await dispatch(getComponentBasic({ token: userdetails.token }))
        : await dispatch(
            getComponentDetails({
              token: userdetails.token,
              level1: level1,
              level2: level2,
              level3: level3,
            })
          );

    if (!data.error) {
      setComponentBasic(data.payload);
    } else {
      console.log(data.error);
    }
  };

  const updateData = (title) => {
    if (title) {
      level3
        ? navigate(`/components/${level1}/${level2}/${title}`)
        : level2
        ? navigate(`/components/${level1}/${title}`)
        : level1
        ? navigate(`/components/${title}`)
        : navigate(`/components`);
    }
    getComponentData();
    setShowEdit(false);
    setShowComponentForm(false);
  };

  useEffect(() => {
    getComponentData();
    setShowEdit(false);
    setShowComponentForm(false);
  }, [userdetails, level1, level2, level3]);

  return (
    <>
      {!showEdit && !showComponentForm && (
        <>
          <div className="p-4 t-color ql-editor-contents">
            <h3>
              {componentBasic && componentBasic.componentHeading
                ? componentBasic.componentHeading
                : "Valuable Components for Building Web and Mobile Applications using MERN Stack"}
            </h3>
            {componentBasic && componentBasic.componentDescription ? (
              parse(`${componentBasic.componentDescription}`)
            ) : (
              <>
                <p>When it comes to developing robust and efficient web and</p>
                <p>
                  By incorporating these valuable components into your MERN
                  stack development workflow, you can enhance your productivity,
                </p>
              </>
            )}
            {userdetails.token && (
              <div className="p-3 d-flex flex-column flex-md-row flex-column-reverse justify-content-between gap-2">
                {!level3 && (level1 || level2) && (
                  <Button
                    className="primary-button mt-2 ms-md-auto"
                    //disabled={categoryName === ""}
                    onClick={() => showAddForm()}
                  >
                    Add Child Component
                  </Button>
                )}
                <Button
                  className="primary-button mt-2"
                  //disabled={categoryName === ""}
                  onClick={() => deleteContent()}
                >
                  Delete Content
                </Button>
                <Button
                  className="primary-button mt-2"
                  //disabled={categoryName === ""}
                  onClick={() => showEditForm()}
                >
                  Edit Content
                </Button>
              </div>
            )}
          </div>
        </>
      )}
      {showEdit && (
        <div className="p-4 t-color">
          <CreateComponent
            cancelEdit={cancelEdit}
            componentBasic={componentBasic}
            updateData={updateData}
            level1={level1}
            level2={level2}
            level3={level3}
            formType="update"
          />
        </div>
      )}
      {showComponentForm && (
        <div className="p-4 t-color">
          <CreateComponent
            cancelEdit={cancelEdit}
            // componentBasic={}
            updateData={updateData}
            level1={level1}
            level2={level2}
            level3={level3}
            componentId={componentBasic._id}
            formType="create"
          />
        </div>
      )}
    </>
  );
};

export default ComponentDetails;
